
.listBox_Cntnr {
    padding: 18px 0;
    width: 75vw;
    max-width: 760px;
    min-width: 400px;
    border-radius: 4px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 0 5px 0px black;
    background-color: rgba(0, 100, 100, 0.801);
    /* border: .5px solid red; */
}

.head_Cntnr {
    padding: 5px 0 8px 0;
    width: 100%;
    background-color: lightgray;
}

.head {
    font-size: 30px;
    font-weight: 600;
    letter-spacing: 1px;
    text-align: center;
    text-shadow: 0 0 5px white;
}

.input-addBtn_Cntnr {
    margin: 20px 0;
    padding: 0 15px;
    width: 85%;
    display: flex;
    justify-content: space-between;
    /* border: 1px solid black; */
}

.input-addBtn_Cntnr input {
    padding: 3px 10px;
    width: 50%;
    font-size: 16px;
    letter-spacing: .5px;
    border: none;
    border-radius: 2px;
}

.addBtn {
    padding: 3px 8px;
    font-size: 16px;
    font-weight: 500;
    letter-spacing: 0.5px;
    cursor: pointer;
}

hr {
    margin-bottom: 15px;
    width: 85%;
}

.list_Cntnr {
    width: 85%;
    justify-content: space-between;
    /* border: 1px solid yellow; */
}

.clearBtn_Cntnr {
    width: 85%;
    display: flex;
    /* border: 1px solid yellow; */
}
.clearBtn {
    padding: 3px 6px 2px 6px;
    font-size: 14px;
    letter-spacing: 0.5px;
    cursor: pointer;
    color: #EAEDED;
    border: none;
    border-radius: 2px;
    background-color: red;
}
.clearBtn:hover {
    color: black;
    background-color: #EAEDED;
}
.clearBtn_Cntnr-non {
    display: none;
}

@media screen and (max-width: 450px) {
    .listBox_Cntnr {
        margin: 0 10px;
        padding: 18px 0;
        min-width: 250px;
        box-shadow: 0 0 3px 0px black;
        background-color: rgba(0, 80, 100, 0.801);
        /* border: .5px solid red; */
    }

    .head {
        font-size: 25px;
    }
}