.item-date-time-deleteBtn_Cntnr {
    padding: 4px 0;
    margin-bottom: 12px;
    border-radius: 1px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #EAEDED;
    /* border: 0.5px solid red; */
}

.task {
    width: 34%;
    padding-left: 5px;
    font-size: 17px;
    /* border: 1px solid red; */
}

.date-time-deleteBtn_Cntnr {
    display: flex;
    justify-content: center;
    align-items: center;
    /* border: .5px solid blue; */
}

.date-time {
    margin-right: 8px;
    font-size: 14px;
    font-weight: 500;
    /* border: 1.5px solid yellow; */
}

.deleteBtn {
    margin: 0 12px;
    padding: 4px 6px 3px 6px;
    font-size: 13px;
    cursor: pointer;
}


@media screen and (max-width: 450px) {
    .task {
        width: auto;
        padding: 0 3px;
        font-size: 16px;
        /* border: 1px solid red; */
    }
    .date-time_Cntnr {
        /* display: flex; */
        flex-direction: column;
        /* border: .5px solid blue; */
    }

    .date-time {
        margin-right: 5px;
        font-size: 12px;
        font-weight: 500;
        /* border: .5px solid black; */
    }
}

@media screen and (max-width: 360px) {
    .date-time_Cntnr {
        display: flex;
        flex-direction: column;
        /* border: .5px solid blue; */
    }
}